* {
  box-sizing: border-box;
}
html {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
}

body {
  margin: 0;
  background-color: #97f4c8;
}

.App {
  padding: 0px;
  margin: 0;

  text-align: center;
}

.App-table {
  width: 100%;
  margin: 0;

  &__list {
    display: flex;
    flex-direction: column;
    padding: 0;
    
    list-style: none;
  }
  &__item {
    margin: 0 5px 5px;
  }
}
